.blurbBox{
    margin: 50px 500px;
}

.title {
    font-size: 18px;
}

.procor,
.profile {
    margin: 30px;
    border-radius: 50%;
    border: 5px solid #555;
}

a:hover{
    color:#333;
    text-decoration: none;
    opacity:0.7;
}

.arrow{
    text-decoration: none;
    font-size: 50px;
    color: black;
    text-align: center;
    margin-bottom: 80px;
}

html{
    scroll-behavior: smooth;
}

.homeGithub{
    text-align: center;
    text-decoration: none;
    color: #333;
    font-size: 40px;
    padding-right: 10px;
}

.homeLinkedin{
    text-align: center;
    text-decoration: none;
    color: #333;
    font-size: 40px;
    padding-right: 10px;
}

@media screen and (max-width: 768px){
    .blurbBox{
        margin: 0px;
    }

    .title {
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        margin-bottom: 0px;
        margin-top: 0px;
    }

    .profile {
        width: 80%;
        margin: 0px;
    }

    .homeGithub{
        text-align: center;
        text-decoration: none;
        color: #333;
        font-size: 20px;
        padding-right: 10px;
    }
    
    .homeLinkedin{
        text-align: center;
        text-decoration: none;
        color: #333;
        font-size: 20px;
        padding-right: 10px;
    }

    .arrow{
        text-decoration: none;
        font-size: 50px;
        color: black;
        text-align: center;
        margin-bottom: 80px;
    }
}
